import React, { useState } from 'react';
import {
  Box,
  Container,
  Typography,
  TextField,
  Button,
  RadioGroup,
  FormControlLabel,
  Radio,
  FormControl,
  FormLabel
} from '@mui/material';

export default function Checkout() {
  const [paymentMethod, setPaymentMethod] = useState('card');
  const [formData, setFormData] = useState({
    cardNumber: '',
    name: '',
    cvv: '',
    expirationDate: '',
    routingNumber: '',
    accountNumber: '',
    thirdPartyLogin: ''
  });

  const handlePayment = () => {
    // 模拟支付处理
    alert('Payment processed successfully!');
  };

  return (
    <Container maxWidth="sm">
      <Box sx={{ mt: 4 }}>
        <Typography variant="h4" gutterBottom>
          Checkout
        </Typography>
        <FormControl component="fieldset">
          <FormLabel component="legend">Payment Method</FormLabel>
          <RadioGroup
            value={paymentMethod}
            onChange={(e) => setPaymentMethod(e.target.value)}
          >
            <FormControlLabel value="card" control={<Radio />} label="Credit/Debit Card" />
            <FormControlLabel value="wire" control={<Radio />} label="Wire Transfer" />
            <FormControlLabel value="thirdParty" control={<Radio />} label="Third Party" />
          </RadioGroup>
        </FormControl>

        {paymentMethod === 'card' && (
          <>
            <TextField
              fullWidth
              label="Card Number"
              margin="normal"
              value={formData.cardNumber}
              onChange={(e) => setFormData({ ...formData, cardNumber: e.target.value })}
            />
            <TextField
              fullWidth
              label="Name on Card"
              margin="normal"
              value={formData.name}
              onChange={(e) => setFormData({ ...formData, name: e.target.value })}
            />
            <TextField
              fullWidth
              label="CVV"
              margin="normal"
              value={formData.cvv}
              onChange={(e) => setFormData({ ...formData, cvv: e.target.value })}
            />
            <TextField
              fullWidth
              label="Expiration Date"
              margin="normal"
              value={formData.expirationDate}
              onChange={(e) => setFormData({ ...formData, expirationDate: e.target.value })}
            />
          </>
        )}

        {paymentMethod === 'wire' && (
          <>
            <TextField
              fullWidth
              label="Routing Number"
              margin="normal"
              value={formData.routingNumber}
              onChange={(e) => setFormData({ ...formData, routingNumber: e.target.value })}
            />
            <TextField
              fullWidth
              label="Account Number"
              margin="normal"
              value={formData.accountNumber}
              onChange={(e) => setFormData({ ...formData, accountNumber: e.target.value })}
            />
          </>
        )}

        {paymentMethod === 'thirdParty' && (
          <TextField
            fullWidth
            label="Third Party Login"
            margin="normal"
            value={formData.thirdPartyLogin}
            onChange={(e) => setFormData({ ...formData, thirdPartyLogin: e.target.value })}
          />
        )}

        <Button
          variant="contained"
          color="primary"
          fullWidth
          sx={{ mt: 3 }}
          onClick={handlePayment}
        >
          Pay Now
        </Button>
      </Box>
    </Container>
  );
} 